"use client";

import React, { useEffect, useMemo, useState } from "react";
import { API_BASE, getAccessToken, getAuthJSON } from "@/utils/api";
import { Input } from "@/components/ui/input";
import { Button } from "@/components/ui/button";
import { Skeleton } from "@/components/ui/skeleton";
import { SectionCard, StatCard } from "../_components/cards";
import { useAffiliateDashboardContext } from "../_context/affiliate-dashboard-context";

type Me = { id: string; tenant_id?: string };

type CommissionRow = {
  id: string;
  affiliate: string;
  amount: number;
  currency: string;
  status: string;
  earned_at: string;
};

const statusOptions = [
  { value: "all", label: "All statuses" },
  { value: "pending", label: "Pending" },
  { value: "approved", label: "Approved" },
  { value: "paid", label: "Paid" },
];

const rangeOptions = [
  { value: "all", label: "All time" },
  { value: "7", label: "Last 7 days" },
  { value: "30", label: "Last 30 days" },
  { value: "90", label: "Last 90 days" },
];

export default function CommissionsListPage() {
  const { stats, monthLabel } = useAffiliateDashboardContext();

  const [loading, setLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);
  const [_me, setMe] = useState<Me | null>(null);
  const [rows, setRows] = useState<CommissionRow[]>([]);

  const [q, setQ] = useState("");
  const [status, setStatus] = useState<string>("all");
  const [range, setRange] = useState<string>("all");
  const [page, setPage] = useState(1);
  const [pageSize, setPageSize] = useState(10);

  useEffect(() => {
    let active = true;
    async function load() {
      try {
        const me = await getAuthJSON<Me>("/api/v1/auth/me");
        if (!active) return;
        setMe(me);

        const token = getAccessToken();
        const res = await fetch(`${API_BASE}/api/v1/admin/commissions`, {
          headers: {
            "Content-Type": "application/json",
            ...(token ? { Authorization: `Bearer ${token}` } : {}),
            ...(me?.tenant_id ? { "X-Tenant-ID": me.tenant_id } : {}),
          },
        });
        if (!res.ok) throw new Error(await res.text().catch(() => "Failed to load"));
        const data = (await res.json()) as CommissionRow[];
        if (!active) return;
        setRows(data);
      } catch (err: any) {
        if (!active) return;
        setError(err?.message || "Failed to load commissions");
      } finally {
        if (active) setLoading(false);
      }
    }
    load();
    return () => {
      active = false;
    };
  }, []);

  const filtered = useMemo(() => {
    const query = q.trim().toLowerCase();
    const now = Date.now();
    let list = rows;
    if (status !== "all") {
      list = list.filter((row) => row.status.toLowerCase() === status);
    }
    if (range !== "all") {
      const days = Number(range);
      const since = now - days * 24 * 60 * 60 * 1000;
      list = list.filter((row) => new Date(row.earned_at).getTime() >= since);
    }
    if (query) {
      list = list.filter((row) => row.affiliate.toLowerCase().includes(query));
    }
    return list;
  }, [rows, q, status, range]);

  const totalPages = Math.max(1, Math.ceil(filtered.length / pageSize));
  const pageSafe = Math.min(page, totalPages);
  const paged = filtered.slice((pageSafe - 1) * pageSize, pageSafe * pageSize);

  useEffect(() => {
    setPage(1);
  }, [q, status, range, pageSize]);

  if (loading) {
    return (
      <div className="space-y-6">
        <Skeleton className="h-40 rounded-3xl" />
        <Skeleton className="h-32 rounded-3xl" />
        <Skeleton className="h-64 rounded-3xl" />
      </div>
    );
  }

  if (error) {
    return <div className="rounded-2xl border border-red-200 bg-red-50/80 p-6 text-sm text-red-600">{error}</div>;
  }

  const totalCommissions = filtered.reduce((sum, row) => sum + (row.amount || 0), 0);

  return (
    <div className="space-y-6">
      <SectionCard
        title="Commission overview"
        description={`You can track performance for ${monthLabel.toLowerCase()} and comparison ranges.`}
      >
        <div className="grid gap-4 sm:grid-cols-2 xl:grid-cols-4">
          <StatCard
            title="This period"
            value={formatCurrency(stats.currentCommission)}
            description="Commission earned for the current month"
          />
          <StatCard
            title="Pending"
            value={formatCurrency(stats.pendingPayout)}
            description="Awaiting manual approval"
          />
          <StatCard
            title="Paid to date"
            value={formatCurrency(stats.paidPayout)}
            description="Lifetime cleared payouts"
          />
          <StatCard
            title="Filtered total"
            value={formatCurrency(totalCommissions)}
            description={`Across ${filtered.length} record${filtered.length === 1 ? "" : "s"}`}
          />
        </div>
      </SectionCard>

      <SectionCard title="Filter commissions" description="Narrow the dataset to focus on recent activity or statuses of interest.">
        <div className="flex flex-col gap-3 lg:flex-row lg:items-end lg:justify-between">
          <div className="flex flex-col gap-2 lg:flex-row lg:items-center">
            <Input
              placeholder="Search affiliate name"
              value={q}
              onChange={(event) => setQ(event.target.value)}
              className="w-full rounded-xl border-emerald-200 focus:border-emerald-400 focus:ring-emerald-400 lg:w-64"
            />

            <select
              value={status}
              onChange={(event) => setStatus(event.target.value)}
              className="h-11 rounded-xl border border-emerald-200 bg-white/90 px-3 text-sm text-emerald-800 focus:border-emerald-400 focus:outline-none"
            >
              {statusOptions.map((option) => (
                <option key={option.value} value={option.value}>
                  {option.label}
                </option>
              ))}
            </select>

            <select
              value={range}
              onChange={(event) => setRange(event.target.value)}
              className="h-11 rounded-xl border border-emerald-200 bg-white/90 px-3 text-sm text-emerald-800 focus:border-emerald-400 focus:outline-none"
            >
              {rangeOptions.map((option) => (
                <option key={option.value} value={option.value}>
                  {option.label}
                </option>
              ))}
            </select>

            <select
              value={pageSize}
              onChange={(event) => setPageSize(Number(event.target.value))}
              className="h-11 rounded-xl border border-emerald-200 bg-white/90 px-3 text-sm text-emerald-800 focus:border-emerald-400 focus:outline-none"
            >
              {[10, 25, 50].map((size) => (
                <option key={size} value={size}>
                  {size} per page
                </option>
              ))}
            </select>
          </div>

          <div className="flex gap-2">
            <Button
              type="button"
              variant="outline"
              className="rounded-xl border-emerald-200 text-emerald-700 hover:bg-emerald-50"
              onClick={() => {
                setQ("");
                setStatus("all");
                setRange("all");
              }}
            >
              Reset filters
            </Button>
          </div>
        </div>
      </SectionCard>

      <SectionCard
        title="Commission history"
        description={`Page ${pageSafe} of ${totalPages}. Displaying ${paged.length} result${paged.length === 1 ? "" : "s"}.`}
      >
        {paged.length === 0 ? (
          <div className="rounded-2xl border border-dashed border-emerald-200 bg-emerald-50/60 p-8 text-center text-sm text-emerald-700/80">
            No commissions match your filters. Adjust search terms or time range to see more activity.
          </div>
        ) : (
          <div className="overflow-hidden rounded-2xl border border-emerald-100/70">
            <table className="min-w-full divide-y divide-emerald-100/70 text-sm text-emerald-900">
              <thead className="bg-emerald-50/70 text-xs uppercase tracking-wide text-emerald-600">
                <tr>
                  <th className="px-4 py-3 text-left">Affiliate</th>
                  <th className="px-4 py-3 text-left">Amount</th>
                  <th className="px-4 py-3 text-left">Currency</th>
                  <th className="px-4 py-3 text-left">Status</th>
                  <th className="px-4 py-3 text-left">Earned on</th>
                </tr>
              </thead>
              <tbody className="divide-y divide-emerald-50/80 bg-white">
                {paged.map((row) => (
                  <tr key={row.id} className="hover:bg-emerald-50/40">
                    <td className="px-4 py-3 font-medium text-emerald-900">{row.affiliate}</td>
                    <td className="px-4 py-3 text-emerald-900">{formatCurrency(row.amount)}</td>
                    <td className="px-4 py-3 text-emerald-700">{row.currency}</td>
                    <td className="px-4 py-3">
                      <StatusBadge status={row.status} />
                    </td>
                    <td className="px-4 py-3 text-xs text-emerald-600">{new Date(row.earned_at).toLocaleDateString()}</td>
                  </tr>
                ))}
              </tbody>
            </table>
          </div>
        )}

        <div className="mt-4 flex flex-wrap items-center justify-between gap-3 text-sm text-emerald-700/80">
          <div>
            Page {pageSafe} of {totalPages} · {filtered.length} total row{filtered.length === 1 ? "" : "s"}
          </div>
          <div className="flex gap-2">
            <Button
              type="button"
              variant="outline"
              onClick={() => setPage((prev) => Math.max(1, prev - 1))}
              disabled={pageSafe <= 1}
              className="rounded-xl border-emerald-200 text-emerald-700 hover:bg-emerald-50"
            >
              Previous
            </Button>
            <Button
              type="button"
              variant="outline"
              onClick={() => setPage((prev) => Math.min(totalPages, prev + 1))}
              disabled={pageSafe >= totalPages}
              className="rounded-xl border-emerald-200 text-emerald-700 hover:bg-emerald-50"
            >
              Next
            </Button>
          </div>
        </div>
      </SectionCard>
    </div>
  );
}

function StatusBadge({ status }: { status: string }) {
  const tone = status?.toLowerCase();
  const styles =
    tone === "approved"
      ? "bg-emerald-50 text-emerald-700"
      : tone === "paid"
      ? "bg-blue-50 text-blue-600"
      : "bg-amber-50 text-amber-700";

  return (
    <span className={`inline-flex items-center rounded-full px-3 py-1 text-xs font-semibold ${styles}`}>
      {status || "Pending"}
    </span>
  );
}

function formatCurrency(amount: number) {
  return new Intl.NumberFormat("en-US", { style: "currency", currency: "USD" }).format(amount || 0);
}
